// claim
export const claim = async (col) => {
  ctx.event.emit('showLoading', 'CLAIMING');
  const { BumperFarmContract, chainAccount } = ctx.data;
  const farmIns = await BumperFarmContract.at(BumperFarm)
  try {
    let res = await farmIns.claimRewardsForPools(
      [LP_ADDRESS[col]],
      {
        from: chainAccount
      }
    );
    alertSuccess('CLAIMING SUCCESS');
    return res;
  } catch (err) {
    alertError(err.message)
  } finally {
    ctx.event.emit('hideLoading');
  }
}